"use client"

import type React from "react"
import { useState, useEffect } from "react"
import { db, storage } from "@/lib/firebase"
import { collection, addDoc, updateDoc, deleteDoc, doc, query, orderBy, onSnapshot, getDocs, setDoc, Timestamp } from "firebase/firestore"
import { ref, uploadBytes, getDownloadURL, deleteObject } from "firebase/storage"
import { Star, MapPin, Users, LogOut, ImageIcon, Home, Folder, FolderOpen, Layers, ChevronRight, ChevronDown } from "lucide-react"
import {
  Plus,
  Edit,
  Trash2,
  Upload,
  X,
  Save,
  ArrowUp,
  ArrowDown,
  AlertCircle,
  CheckCircle,
  User,
  ChevronUp,
  ChevronDown as DownIcon,
  Eye,
  EyeOff,
  Menu,
  X as CloseIcon,
} from "lucide-react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"

interface Tab {
  id: string
  name: string
  type: "main" | "sub"
  order: number
  parentId?: string
  createdAt: string
  updatedAt: string
}

interface TeamMember {
  id: string
  name: string
  designation: string
  location: string
  image: string
  tabId: string
  subTabId?: string
  order: number
  createdAt?: string
  updatedAt?: string
  imagePath?: string
}

interface Banner {
  id: string
  imageUrl: string
  title?: string
  description?: string
  order: number
  active: boolean
  imagePath?: string
}

export default function AdminDashboard() {
  const [tabs, setTabs] = useState<Tab[]>([])
  const [teamMembers, setTeamMembers] = useState<TeamMember[]>([])
  const [banners, setBanners] = useState<Banner[]>([])
  const [editingMember, setEditingMember] = useState<TeamMember | null>(null)
  const [editingTab, setEditingTab] = useState<Tab | null>(null)
  const [activeTab, setActiveTab] = useState<"team" | "banners" | "structure">("team")
  const [selectedMainTab, setSelectedMainTab] = useState<string | null>(null)
  const [expandedTabs, setExpandedTabs] = useState<string[]>([])
  
  const [newMember, setNewMember] = useState({
    name: "",
    designation: "",
    location: "",
    image: "",
    tabId: "",
    subTabId: "",
    order: 0,
  })
  
  const [newTab, setNewTab] = useState({
    name: "",
    type: "main" as "main" | "sub",
    parentId: "",
  })
  
  const [newBanner, setNewBanner] = useState({
    title: "",
    description: "",
    order: 0,
    active: true
  })
  
  const [uploading, setUploading] = useState(false)
  const [loading, setLoading] = useState(true)
  const [imageFile, setImageFile] = useState<File | null>(null)
  const [editingImageFile, setEditingImageFile] = useState<File | null>(null)
  const [bannerImageFile, setBannerImageFile] = useState<File | null>(null)
  const [error, setError] = useState<string | null>(null)
  const [success, setSuccess] = useState<string | null>(null)
  const [uploadProgress, setUploadProgress] = useState(0)
  const [previewUrl, setPreviewUrl] = useState<string | null>(null)
  const [editingPreviewUrl, setEditingPreviewUrl] = useState<string | null>(null)
  const [bannerPreviewUrl, setBannerPreviewUrl] = useState<string | null>(null)
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const router = useRouter()

  useEffect(() => {
    checkAuth()
  }, [])

  async function checkAuth() {
    try {
      const response = await fetch("/api/admin/check-auth")
      if (!response.ok) {
        router.push("/dashboard/login")
      }
    } catch (error) {
      router.push("/dashboard/login")
    } finally {
      setLoading(false)
    }
  }

  // Clear object URLs on unmount
  useEffect(() => {
    return () => {
      if (previewUrl) URL.revokeObjectURL(previewUrl)
      if (editingPreviewUrl) URL.revokeObjectURL(editingPreviewUrl)
      if (bannerPreviewUrl) URL.revokeObjectURL(bannerPreviewUrl)
    }
  }, [previewUrl, editingPreviewUrl, bannerPreviewUrl])

  // Load tabs
  useEffect(() => {
    const loadTabs = async () => {
      try {
        const q = query(collection(db, "tabs"), orderBy("order"))
        const querySnapshot = await getDocs(q)
        
        const tabsData: Tab[] = []
        querySnapshot.forEach((doc) => {
          const data = doc.data()
          tabsData.push({
            id: doc.id,
            name: data.name || "",
            type: data.type || "main",
            order: data.order || 0,
            parentId: data.parentId || "",
            createdAt: data.createdAt || "",
            updatedAt: data.updatedAt || "",
          })
        })
        
        setTabs(tabsData)
        
        // Set first main tab as selected by default
        const firstMainTab = tabsData.find(t => t.type === "main")
        if (firstMainTab) {
          setSelectedMainTab(firstMainTab.id)
        }
      } catch (err: any) {
        console.error("Error loading tabs:", err)
      }
    }

    loadTabs()
  }, [])

  // Load team members
  useEffect(() => {
    const loadTeamMembers = async () => {
      try {
        const q = query(collection(db, "teamMembers"), orderBy("order"))
        const querySnapshot = await getDocs(q)

        const members: TeamMember[] = []
        querySnapshot.forEach((doc) => {
          const data = doc.data()
          members.push({
            id: doc.id,
            name: data.name || "",
            designation: data.designation || "",
            location: data.location || "",
            image: data.image || "",
            tabId: data.tabId || "",
            subTabId: data.subTabId || "",
            order: data.order || 0,
            createdAt: data.createdAt || "",
            updatedAt: data.updatedAt || "",
            imagePath: data.imagePath || "",
          })
        })

        members.sort((a, b) => (a.order || 0) - (b.order || 0))
        setTeamMembers(members)
      } catch (err: any) {
        console.error("Error loading team members:", err)
        setError(`Failed to load team members: ${err.message}`)
      }
    }

    loadTeamMembers()
  }, [])

  // Load banners
  useEffect(() => {
    const loadBanners = async () => {
      try {
        const q = query(collection(db, "homeBanners"), orderBy("order"))
        const querySnapshot = await getDocs(q)
        
        const bannersData: Banner[] = []
        querySnapshot.forEach((doc) => {
          bannersData.push({
            id: doc.id,
            ...doc.data()
          } as Banner)
        })
        
        setBanners(bannersData)
      } catch (err: any) {
        console.error("Error loading banners:", err)
      }
    }

    loadBanners()
  }, [])

  // Upload image to Firebase Storage
  const uploadImageToFirebaseStorage = async (file: File, existingImagePath?: string, folder: "team-members" | "home-banners" = "team-members"): Promise<{url: string, path: string}> => {
    setUploading(true)
    setUploadProgress(0)
    setError(null)

    try {
      // Validate file
      if (!file.type.startsWith("image/")) {
        throw new Error("Please select an image file (JPEG, PNG, WebP, etc.)")
      }

      if (file.size > 5 * 1024 * 1024) {
        throw new Error("Image size should be less than 5MB")
      }

      // Simulate upload progress
      const progressInterval = setInterval(() => {
        setUploadProgress((prev) => {
          if (prev >= 90) {
            clearInterval(progressInterval)
            return 90
          }
          return prev + 10
        })
      }, 200)

      // Generate unique filename
      const timestamp = Date.now()
      const randomString = Math.random().toString(36).substring(2, 15)
      const sanitizedName = file.name.replace(/[^a-zA-Z0-9.-]/g, '_')
      const fileName = `${timestamp}_${randomString}_${sanitizedName}`
      const storagePath = `${folder}/${fileName}`
      
      // Create a reference to the file location
      const storageRef = ref(storage, storagePath)
      
      // Upload the file
      const snapshot = await uploadBytes(storageRef, file)
      
      // Get the download URL
      const downloadURL = await getDownloadURL(snapshot.ref)
      
      clearInterval(progressInterval)
      setUploadProgress(100)

      // Delete old image if it exists and is different from new one
      if (existingImagePath && existingImagePath !== storagePath) {
        try {
          const oldImageRef = ref(storage, existingImagePath)
          await deleteObject(oldImageRef)
        } catch (deleteError) {
          console.warn("⚠️ Could not delete old image:", deleteError)
        }
      }

      // Wait a bit to show 100% progress
      await new Promise((resolve) => setTimeout(resolve, 500))

      return { url: downloadURL, path: storagePath }
    } catch (error: any) {
      console.error("❌ Error uploading image:", error)
      throw new Error(`Failed to upload image: ${error.message || "Unknown error"}`)
    } finally {
      setUploading(false)
      setTimeout(() => setUploadProgress(0), 1000)
    }
  }

  async function handleLogout() {
    await fetch("/api/admin/logout", { method: "POST" })
    router.push("/dashboard/login")
  }

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>, isEditing = false, isBanner = false) => {
    const file = e.target.files?.[0]
    if (!file) return

    setError(null)

    if (!file.type.startsWith("image/")) {
      setError("Please select an image file (JPEG, PNG, WebP, etc.)")
      return
    }

    if (file.size > 5 * 1024 * 1024) {
      setError("Image size should be less than 5MB")
      return
    }

    const objectUrl = URL.createObjectURL(file)

    if (isBanner) {
      setBannerImageFile(file)
      setBannerPreviewUrl(objectUrl)
    } else if (isEditing) {
      setEditingImageFile(file)
      setEditingPreviewUrl(objectUrl)
    } else {
      setImageFile(file)
      setPreviewUrl(objectUrl)
      setNewMember((prev) => ({ ...prev, image: objectUrl }))
    }
  }

  // Tab Management Functions
  const handleAddTab = async () => {
    if (!newTab.name.trim()) {
      setError("Please enter tab name")
      return
    }

    try {
      setError(null)
      
      const timestamp = new Date().toISOString()
      
      // Get next order number
      let order = 0
      if (newTab.type === "main") {
        const mainTabs = tabs.filter(t => t.type === "main")
        order = mainTabs.length > 0 ? Math.max(...mainTabs.map(t => t.order)) + 1 : 0
      } else if (newTab.type === "sub" && newTab.parentId) {
        const subTabs = tabs.filter(t => t.type === "sub" && t.parentId === newTab.parentId)
        order = subTabs.length > 0 ? Math.max(...subTabs.map(t => t.order)) + 1 : 0
      }

      const tabData = {
        name: newTab.name.trim(),
        type: newTab.type,
        order,
        parentId: newTab.type === "sub" ? newTab.parentId : "",
        createdAt: timestamp,
        updatedAt: timestamp,
      }

      const docRef = await addDoc(collection(db, "tabs"), tabData)
      
      // Reset form
      setNewTab({
        name: "",
        type: "main",
        parentId: "",
      })

      // Reload tabs
      const q = query(collection(db, "tabs"), orderBy("order"))
      const querySnapshot = await getDocs(q)
      
      const tabsData: Tab[] = []
      querySnapshot.forEach((doc) => {
        const data = doc.data()
        tabsData.push({
          id: doc.id,
          name: data.name || "",
          type: data.type || "main",
          order: data.order || 0,
          parentId: data.parentId || "",
          createdAt: data.createdAt || "",
          updatedAt: data.updatedAt || "",
        })
      })
      
      setTabs(tabsData)
      
      setSuccess("✅ Tab added successfully!")
      setTimeout(() => setSuccess(null), 3000)
    } catch (err: any) {
      setError(`Failed to add tab: ${err.message}`)
    }
  }

  const handleUpdateTab = async () => {
    if (!editingTab) return

    try {
      setError(null)
      
      const timestamp = new Date().toISOString()
      
      const tabData = {
        name: editingTab.name.trim(),
        type: editingTab.type,
        order: editingTab.order,
        parentId: editingTab.parentId || "",
        updatedAt: timestamp,
      }

      await updateDoc(doc(db, "tabs", editingTab.id), tabData)
      
      setEditingTab(null)
      
      // Reload tabs
      const q = query(collection(db, "tabs"), orderBy("order"))
      const querySnapshot = await getDocs(q)
      
      const tabsData: Tab[] = []
      querySnapshot.forEach((doc) => {
        const data = doc.data()
        tabsData.push({
          id: doc.id,
          name: data.name || "",
          type: data.type || "main",
          order: data.order || 0,
          parentId: data.parentId || "",
          createdAt: data.createdAt || "",
          updatedAt: data.updatedAt || "",
        })
      })
      
      setTabs(tabsData)
      
      setSuccess("✅ Tab updated successfully!")
      setTimeout(() => setSuccess(null), 3000)
    } catch (err: any) {
      setError(`Failed to update tab: ${err.message}`)
    }
  }

  const handleDeleteTab = async (tab: Tab) => {
    if (!confirm(`Are you sure you want to delete "${tab.name}"? This will also delete all team members in this tab.`)) return

    try {
      // Delete all team members in this tab
      const membersToDelete = teamMembers.filter(member => 
        member.tabId === tab.id || (tab.type === "main" && member.subTabId && tabs.find(t => t.id === member.subTabId)?.parentId === tab.id)
      )
      
      // Delete subtabs if it's a main tab
      if (tab.type === "main") {
        const subTabs = tabs.filter(t => t.parentId === tab.id)
        for (const subTab of subTabs) {
          await deleteDoc(doc(db, "tabs", subTab.id))
        }
      }
      
      // Delete the tab
      await deleteDoc(doc(db, "tabs", tab.id))
      
      // Delete team members
      for (const member of membersToDelete) {
        if (member.imagePath) {
          try {
            const imageRef = ref(storage, member.imagePath)
            await deleteObject(imageRef)
          } catch (err) {
            console.warn("Could not delete image:", err)
          }
        }
        await deleteDoc(doc(db, "teamMembers", member.id))
      }

      // Update state
      setTabs(tabs.filter(t => t.id !== tab.id && t.parentId !== tab.id))
      setTeamMembers(teamMembers.filter(member => 
        member.tabId !== tab.id && (tab.type !== "main" || !member.subTabId || tabs.find(t => t.id === member.subTabId)?.parentId !== tab.id)
      ))
      
      setSuccess("✅ Tab and associated content deleted successfully!")
      setTimeout(() => setSuccess(null), 3000)
    } catch (err: any) {
      setError(`Failed to delete tab: ${err.message}`)
    }
  }

  const handleReorderTab = async (index: number, direction: "up" | "down") => {
    const sortedTabs = [...tabs].sort((a, b) => a.order - b.order)
    const targetIndex = direction === "up" ? index - 1 : index + 1

    if (targetIndex < 0 || targetIndex >= sortedTabs.length) return

    try {
      const tab1 = sortedTabs[index]
      const tab2 = sortedTabs[targetIndex]

      // Swap orders
      const tempOrder = tab1.order
      tab1.order = tab2.order
      tab2.order = tempOrder

      // Update in Firebase
      await Promise.all([
        updateDoc(doc(db, "tabs", tab1.id), {
          order: tab1.order,
          updatedAt: new Date().toISOString(),
        }),
        updateDoc(doc(db, "tabs", tab2.id), {
          order: tab2.order,
          updatedAt: new Date().toISOString(),
        }),
      ])

      // Update local state
      setTabs([...sortedTabs].sort((a, b) => a.order - b.order))
    } catch (err: any) {
      setError(`Failed to reorder: ${err.message}`)
    }
  }

  const handleAddMember = async () => {
    if (!newMember.name.trim()) {
      setError("Please enter team member name")
      return
    }

    if (!newMember.designation.trim()) {
      setError("Please enter designation")
      return
    }

    if (!newMember.location.trim()) {
      setError("Please enter location")
      return
    }

    if (!newMember.tabId) {
      setError("Please select a tab")
      return
    }

    try {
      setUploading(true)
      setError(null)

      let imageUrl = ""
      let imagePath = ""

      if (imageFile) {
        try {
          const firebaseResult = await uploadImageToFirebaseStorage(imageFile)
          imageUrl = firebaseResult.url
          imagePath = firebaseResult.path
        } catch (uploadError: any) {
          setError(`Image upload failed: ${uploadError.message}`)
          setUploading(false)
          return
        }
      } else if (newMember.image && newMember.image.startsWith("blob:")) {
        setError("Please select an image file to upload")
        setUploading(false)
        return
      }

      const selectedTab = tabs.find(t => t.id === newMember.tabId)
      const subTabId = selectedTab?.type === "main" && newMember.subTabId ? newMember.subTabId : ""

      const highestOrder = teamMembers.filter(m => 
        m.tabId === newMember.tabId && m.subTabId === subTabId
      ).length

      const memberData: any = {
        name: newMember.name.trim(),
        designation: newMember.designation.trim(),
        location: newMember.location.trim(),
        tabId: newMember.tabId,
        order: highestOrder,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
      }

      if (subTabId) {
        memberData.subTabId = subTabId
      }

      if (imageUrl) {
        memberData.image = imageUrl
        memberData.imagePath = imagePath
      }

      const docRef = await addDoc(collection(db, "teamMembers"), memberData)

      clearForm()

      setSuccess("✅ Team member added successfully!")
      setTimeout(() => setSuccess(null), 5000)
    } catch (error: any) {
      console.error("Error adding team member:", error)
      setError(`Failed to add team member: ${error.message}`)
    } finally {
      setUploading(false)
    }
  }

  const handleUpdateMember = async () => {
    if (!editingMember) return

    setError(null)
    setSuccess(null)
    setUploading(true)

    try {
      let imageUrl = editingMember.image || ""
      let imagePath = editingMember.imagePath || ""

      if (editingImageFile) {
        try {
          const firebaseResult = await uploadImageToFirebaseStorage(editingImageFile, editingMember.imagePath)
          imageUrl = firebaseResult.url
          imagePath = firebaseResult.path
        } catch (uploadError: any) {
          setError(`Image upload failed: ${uploadError.message}`)
          setUploading(false)
          return
        }
      }

      const memberData: any = {
        name: editingMember.name.trim(),
        designation: editingMember.designation.trim(),
        location: editingMember.location.trim(),
        tabId: editingMember.tabId,
        subTabId: editingMember.subTabId || "",
        order: editingMember.order || 0,
        updatedAt: new Date().toISOString(),
      }

      if (imageUrl) {
        memberData.image = imageUrl
        memberData.imagePath = imagePath
      }

      const memberRef = doc(db, "teamMembers", editingMember.id)
      await updateDoc(memberRef, memberData)

      setEditingMember(null)
      setEditingImageFile(null)
      if (editingPreviewUrl) {
        URL.revokeObjectURL(editingPreviewUrl)
        setEditingPreviewUrl(null)
      }

      setSuccess("✅ Team member updated successfully!")
      setTimeout(() => setSuccess(null), 5000)
    } catch (error: any) {
      console.error("Error updating team member:", error)
      setError(`Failed to update team member: ${error.message}`)
    } finally {
      setUploading(false)
    }
  }

  const handleDeleteMember = async (id: string, imagePath?: string) => {
    if (!confirm("Are you sure you want to delete this team member? This action cannot be undone.")) return

    try {
      if (imagePath) {
        try {
          const imageRef = ref(storage, imagePath)
          await deleteObject(imageRef)
        } catch (storageError) {
          console.warn("⚠️ Could not delete image from storage:", storageError)
        }
      }

      await deleteDoc(doc(db, "teamMembers", id))
      setSuccess("✅ Team member deleted successfully!")
      setTimeout(() => setSuccess(null), 3000)
    } catch (error: any) {
      console.error("Error deleting team member:", error)
      setError(`Failed to delete team member: ${error.message}`)
    }
  }

  const handleReorder = async (index: number, direction: "up" | "down", tabId: string, subTabId?: string) => {
    const members = [...teamMembers].filter(m => 
      m.tabId === tabId && m.subTabId === subTabId
    ).sort((a, b) => a.order - b.order)
    
    const targetIndex = direction === "up" ? index - 1 : index + 1

    if (targetIndex < 0 || targetIndex >= members.length) return

    try {
      const member1 = members[index]
      const member2 = members[targetIndex]

      const tempOrder = member1.order
      member1.order = member2.order
      member2.order = tempOrder

      await Promise.all([
        updateDoc(doc(db, "teamMembers", member1.id), {
          order: member1.order,
          updatedAt: new Date().toISOString(),
        }),
        updateDoc(doc(db, "teamMembers", member2.id), {
          order: member2.order,
          updatedAt: new Date().toISOString(),
        }),
      ])
    } catch (error: any) {
      console.error("Error updating order:", error)
      setError("Failed to update order. Please refresh the page.")
    }
  }

  // Banner Functions
  const handleAddBanner = async () => {
    if (!bannerImageFile) {
      setError("Please select an image for the banner")
      return
    }

    try {
      setUploading(true)
      setError(null)

      const imageData = await uploadImageToFirebaseStorage(bannerImageFile, undefined, "home-banners")

      const nextOrder = banners.length > 0 
        ? Math.max(...banners.map(b => b.order)) + 1 
        : 0

      const bannerData = {
        imageUrl: imageData.url,
        imagePath: imageData.path,
        title: newBanner.title.trim(),
        description: newBanner.description.trim(),
        order: nextOrder,
        active: newBanner.active,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString()
      }

      const docRef = doc(collection(db, "homeBanners"))
      await setDoc(docRef, bannerData)

      setNewBanner({ title: "", description: "", order: 0, active: true })
      setBannerImageFile(null)
      if (bannerPreviewUrl) {
        URL.revokeObjectURL(bannerPreviewUrl)
        setBannerPreviewUrl(null)
      }
      
      const q = query(collection(db, "homeBanners"), orderBy("order"))
      const querySnapshot = await getDocs(q)
      
      const bannersData: Banner[] = []
      querySnapshot.forEach((doc) => {
        bannersData.push({
          id: doc.id,
          ...doc.data()
        } as Banner)
      })
      
      setBanners(bannersData)
      
      setSuccess("✅ Banner added successfully!")
      setTimeout(() => setSuccess(null), 3000)
    } catch (err: any) {
      setError(`Failed to add banner: ${err.message}`)
    } finally {
      setUploading(false)
    }
  }

  const handleDeleteBanner = async (banner: Banner) => {
    if (!confirm("Are you sure you want to delete this banner?")) return

    try {
      if (banner.imagePath) {
        try {
          const imageRef = ref(storage, banner.imagePath)
          await deleteObject(imageRef)
        } catch (err) {
          console.warn("Could not delete image from storage:", err)
        }
      }

      await deleteDoc(doc(db, "homeBanners", banner.id))
      setBanners(banners.filter(b => b.id !== banner.id))
      
      setSuccess("✅ Banner deleted successfully!")
      setTimeout(() => setSuccess(null), 3000)
    } catch (err: any) {
      setError(`Failed to delete banner: ${err.message}`)
    }
  }

  const handleBannerReorder = async (index: number, direction: "up" | "down") => {
    const newBanners = [...banners]
    const targetIndex = direction === "up" ? index - 1 : index + 1

    if (targetIndex < 0 || targetIndex >= newBanners.length) return

    const tempOrder = newBanners[index].order
    newBanners[index].order = newBanners[targetIndex].order
    newBanners[targetIndex].order = tempOrder

    try {
      await Promise.all([
        setDoc(doc(db, "homeBanners", newBanners[index].id), {
          ...newBanners[index],
          updatedAt: new Date().toISOString()
        }),
        setDoc(doc(db, "homeBanners", newBanners[targetIndex].id), {
          ...newBanners[targetIndex],
          updatedAt: new Date().toISOString()
        })
      ])

      newBanners.sort((a, b) => a.order - b.order)
      setBanners(newBanners)
    } catch (err: any) {
      setError(`Failed to reorder: ${err.message}`)
    }
  }

  const handleToggleActive = async (banner: Banner) => {
    try {
      const updatedBanner = {
        ...banner,
        active: !banner.active,
        updatedAt: new Date().toISOString()
      }

      await setDoc(doc(db, "homeBanners", banner.id), updatedBanner)
      
      setBanners(banners.map(b => 
        b.id === banner.id ? updatedBanner : b
      ))
      
      setSuccess(`✅ Banner ${updatedBanner.active ? 'activated' : 'deactivated'}!`)
      setTimeout(() => setSuccess(null), 3000)
    } catch (err: any) {
      setError(`Failed to update banner: ${err.message}`)
    }
  }

  const clearForm = () => {
    setNewMember({
      name: "",
      designation: "",
      location: "",
      image: "",
      tabId: "",
      subTabId: "",
      order: 0,
    })
    setImageFile(null)
    if (previewUrl) {
      URL.revokeObjectURL(previewUrl)
      setPreviewUrl(null)
    }
    setError(null)
  }

  const clearEditingForm = () => {
    setEditingMember(null)
    setEditingImageFile(null)
    if (editingPreviewUrl) {
      URL.revokeObjectURL(editingPreviewUrl)
      setEditingPreviewUrl(null)
    }
  }

  const clearBannerForm = () => {
    setNewBanner({
      title: "",
      description: "",
      order: 0,
      active: true
    })
    setBannerImageFile(null)
    if (bannerPreviewUrl) {
      URL.revokeObjectURL(bannerPreviewUrl)
      setBannerPreviewUrl(null)
    }
  }

  const clearTabForm = () => {
    setNewTab({
      name: "",
      type: "main",
      parentId: "",
    })
  }

  const getMainTabs = () => tabs.filter(tab => tab.type === "main").sort((a, b) => a.order - b.order)
  const getSubTabs = (mainTabId: string) => tabs.filter(tab => tab.type === "sub" && tab.parentId === mainTabId).sort((a, b) => a.order - b.order)

  const toggleExpandTab = (tabId: string) => {
    setExpandedTabs(prev =>
      prev.includes(tabId)
        ? prev.filter(id => id !== tabId)
        : [...prev, tabId]
    )
  }

  const getMembersForTab = (tabId: string, subTabId?: string) => {
    return teamMembers
      .filter(member => member.tabId === tabId && (subTabId ? member.subTabId === subTabId : !member.subTabId))
      .sort((a, b) => a.order - b.order)
  }

  if (loading && teamMembers.length === 0) {
    return (
      <div className="min-h-screen bg-gray-50 p-8">
        <div className="max-w-7xl mx-auto">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-black mx-auto"></div>
            <p className="mt-4 text-gray-600">Loading dashboard...</p>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Mobile Menu Button */}
      <button
        onClick={() => setIsMenuOpen(!isMenuOpen)}
        className="lg:hidden fixed top-4 left-4 z-50 p-2 bg-black text-white rounded-lg"
      >
        {isMenuOpen ? <CloseIcon size={24} /> : <Menu size={24} />}
      </button>

      <div className="flex">
        {/* Sidebar */}
        <div className={`
          fixed lg:static inset-y-0 left-0 z-40
          w-64 bg-white border-r border-gray-200 p-6
          transform ${isMenuOpen ? 'translate-x-0' : '-translate-x-full'} lg:translate-x-0
          transition-transform duration-200 ease-in-out
        `}>
          <div className="mb-8">
            <h2 className="text-xl font-bold text-gray-900 mb-2">Admin Dashboard</h2>
            <p className="text-sm text-gray-600">Manage your content</p>
          </div>

          <nav className="space-y-2">
            <button
              onClick={() => { setActiveTab("team"); setIsMenuOpen(false); }}
              className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg text-left transition-colors ${
                activeTab === "team" ? "bg-black text-white" : "text-gray-700 hover:bg-gray-100"
              }`}
            >
              <Users size={20} />
              <span>Team Management</span>
            </button>
            <button
              onClick={() => { setActiveTab("structure"); setIsMenuOpen(false); }}
              className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg text-left transition-colors ${
                activeTab === "structure" ? "bg-black text-white" : "text-gray-700 hover:bg-gray-100"
              }`}
            >
              <Layers size={20} />
              <span>Tab Structure</span>
            </button>
            <button
              onClick={() => { setActiveTab("banners"); setIsMenuOpen(false); }}
              className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg text-left transition-colors ${
                activeTab === "banners" ? "bg-black text-white" : "text-gray-700 hover:bg-gray-100"
              }`}
            >
              <Home size={20} />
              <span>Banner Management</span>
            </button>
            <button
              onClick={handleLogout}
              className="w-full flex items-center gap-3 px-4 py-3 rounded-lg text-left text-gray-700 hover:bg-gray-100 transition-colors mt-8"
            >
              <LogOut size={20} />
              <span>Logout</span>
            </button>
          </nav>
        </div>

        {/* Main Content */}
        <div className="flex-1 p-4 sm:p-6 lg:p-8">
          <div className="max-w-7xl mx-auto">
            {/* Messages */}
            {error && (
              <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg flex items-center gap-3">
                <AlertCircle className="text-red-500 flex-shrink-0" size={20} />
                <p className="text-red-700 flex-1">{error}</p>
                <button onClick={() => setError(null)} className="ml-2 p-1 text-red-500 hover:text-red-700">
                  <X size={18} />
                </button>
              </div>
            )}

            {success && (
              <div className="mb-6 p-4 bg-green-50 border border-green-200 rounded-lg flex items-center gap-3">
                <CheckCircle className="text-green-500 flex-shrink-0" size={20} />
                <p className="text-green-700 flex-1">{success}</p>
                <button onClick={() => setSuccess(null)} className="ml-2 p-1 text-green-500 hover:text-green-700">
                  <X size={18} />
                </button>
              </div>
            )}

            {/* Upload Progress */}
            {uploading && uploadProgress > 0 && (
              <div className="mb-6 p-4 bg-blue-50 border border-blue-200 rounded-lg">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-sm font-medium text-blue-700 flex items-center gap-2">
                    <ImageIcon size={16} />
                    {uploadProgress < 100 ? "Uploading..." : "Processing..."}
                  </span>
                  <span className="text-sm font-bold text-blue-700">{uploadProgress}%</span>
                </div>
                <div className="w-full bg-blue-200 rounded-full h-2">
                  <div
                    className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                    style={{ width: `${uploadProgress}%` }}
                  ></div>
                </div>
              </div>
            )}

            {/* TEAM MANAGEMENT TAB */}
            {activeTab === "team" && (
              <div className="space-y-6">
                {/* Add New Member Form */}
                <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
                  <h2 className="text-xl font-bold text-gray-900 mb-4">Add New Team Member</h2>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {/* Tab Selection */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Select Tab *</label>
                      <select
                        value={newMember.tabId}
                        onChange={(e) => {
                          const tabId = e.target.value
                          setNewMember({
                            ...newMember,
                            tabId,
                            subTabId: "" // Reset subTab when main tab changes
                          })
                        }}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        disabled={uploading}
                      >
                        <option value="">Select a tab</option>
                        {getMainTabs().map((tab) => (
                          <option key={tab.id} value={tab.id}>{tab.name}</option>
                        ))}
                      </select>
                    </div>

                    {/* Sub Tab Selection (if main tab has subtabs) */}
                    {newMember.tabId && getSubTabs(newMember.tabId).length > 0 && (
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Select Sub Tab (Optional)</label>
                        <select
                          value={newMember.subTabId}
                          onChange={(e) => setNewMember({ ...newMember, subTabId: e.target.value })}
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                          disabled={uploading}
                        >
                          <option value="">None (Main Tab)</option>
                          {getSubTabs(newMember.tabId).map((subTab) => (
                            <option key={subTab.id} value={subTab.id}>{subTab.name}</option>
                          ))}
                        </select>
                      </div>
                    )}

                    {/* Name */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Name *</label>
                      <input
                        type="text"
                        value={newMember.name}
                        onChange={(e) => setNewMember({ ...newMember, name: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        placeholder="Enter full name"
                        disabled={uploading}
                      />
                    </div>

                    {/* Designation */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Designation *</label>
                      <input
                        type="text"
                        value={newMember.designation}
                        onChange={(e) => setNewMember({ ...newMember, designation: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        placeholder="Enter designation"
                        disabled={uploading}
                      />
                    </div>

                    {/* Location */}
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Location *</label>
                      <input
                        type="text"
                        value={newMember.location}
                        onChange={(e) => setNewMember({ ...newMember, location: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        placeholder="Enter location"
                        disabled={uploading}
                      />
                    </div>

                    {/* Image Upload */}
                    <div className="md:col-span-2">
                      <label className="block text-sm font-medium text-gray-700 mb-1">Profile Image</label>
                      <div className="flex flex-col gap-4">
                        <div className="flex items-center gap-4">
                          <label
                            className={`flex items-center gap-2 px-4 py-2 rounded-lg cursor-pointer transition-colors ${uploading ? "bg-gray-400 cursor-not-allowed" : "bg-black text-white hover:bg-gray-800"}`}
                          >
                            <Upload size={18} />
                            {uploading ? "Uploading..." : "Select Image"}
                            <input
                              type="file"
                              accept="image/*"
                              onChange={(e) => handleFileChange(e, false)}
                              className="hidden"
                              disabled={uploading}
                            />
                          </label>
                          {imageFile && (
                            <div className="flex items-center gap-2">
                              <span className="text-sm text-gray-600">
                                Selected: {imageFile.name} ({(imageFile.size / 1024).toFixed(2)} KB)
                              </span>
                              <button
                                onClick={() => {
                                  setImageFile(null)
                                  setNewMember({ ...newMember, image: "" })
                                  if (previewUrl) {
                                    URL.revokeObjectURL(previewUrl)
                                    setPreviewUrl(null)
                                  }
                                }}
                                className="p-1 text-red-500 hover:text-red-700"
                                disabled={uploading}
                              >
                                <X size={16} />
                              </button>
                            </div>
                          )}
                        </div>

                        {/* Image Preview */}
                        {(previewUrl || newMember.image) && (
                          <div className="mt-2">
                            <p className="text-sm font-medium text-gray-700 mb-2">Preview:</p>
                            <div className="flex flex-wrap items-start gap-4">
                              <div className="relative w-32 h-32 border border-gray-300 rounded-lg overflow-hidden bg-gray-100">
                                {previewUrl ? (
                                  <img
                                    src={previewUrl}
                                    alt="Preview"
                                    className="w-full h-full object-cover"
                                  />
                                ) : newMember.image ? (
                                  <img
                                    src={newMember.image}
                                    alt="Preview"
                                    className="w-full h-full object-cover"
                                    onError={(e) => {
                                      ;(e.target as HTMLImageElement).style.display = "none"
                                      const parent = e.target.parentElement
                                      if (parent) {
                                        parent.innerHTML =
                                          '<div class="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500">No Image</div>'
                                      }
                                    }}
                                  />
                                ) : null}
                              </div>
                              <div className="text-sm text-gray-600">
                                <p className="font-medium">Image Details:</p>
                                <p>• Will be uploaded to Firebase Storage</p>
                                <p>• Stored in "team-members" folder</p>
                                <p>• Automatically deleted when member is removed</p>
                              </div>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>

                  {/* Form Actions */}
                  <div className="mt-6 flex items-center gap-4">
                    <button
                      onClick={handleAddMember}
                      disabled={uploading || !newMember.tabId}
                      className={`px-6 py-3 rounded-lg font-semibold transition-colors flex items-center gap-2 ${
                        uploading || !newMember.tabId ? "bg-gray-400 cursor-not-allowed" : "bg-black text-white hover:bg-gray-800"
                      }`}
                    >
                      {uploading ? (
                        <>
                          <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                          Processing...
                        </>
                      ) : (
                        <>
                          <Plus size={18} />
                          Add Team Member
                        </>
                      )}
                    </button>
                    <button
                      onClick={clearForm}
                      disabled={uploading}
                      className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors disabled:opacity-50"
                    >
                      Clear Form
                    </button>
                  </div>
                </div>

                {/* Team Members Display */}
                <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
                  <h2 className="text-xl font-bold text-gray-900 mb-6">Team Members</h2>
                  
                  {getMainTabs().map((mainTab) => (
                    <div key={mainTab.id} className="mb-8 last:mb-0">
                      {/* Main Tab Header */}
                      <div className="flex items-center justify-between mb-4">
                        <div className="flex items-center gap-3">
                          <button
                            onClick={() => toggleExpandTab(mainTab.id)}
                            className="p-1 hover:bg-gray-100 rounded-lg transition-colors"
                          >
                            {expandedTabs.includes(mainTab.id) ? (
                              <ChevronDown size={20} />
                            ) : (
                              <ChevronRight size={20} />
                            )}
                          </button>
                          <h3 className="text-lg font-semibold text-gray-900 flex items-center gap-2">
                            <Folder size={20} className="text-gray-500" />
                            {mainTab.name}
                          </h3>
                          <span className="bg-gray-100 text-gray-800 text-sm font-medium px-3 py-1 rounded-full">
                            {getMembersForTab(mainTab.id).length} members
                          </span>
                        </div>
                      </div>

                      {/* Main Tab Members (if expanded) */}
                      {expandedTabs.includes(mainTab.id) && (
                        <div className="ml-8 mb-6">
                          {getMembersForTab(mainTab.id).map((member, index) => (
                            <div
                              key={member.id}
                              className="border border-gray-200 rounded-lg p-4 hover:border-gray-300 transition-colors mb-3"
                            >
                              <div className="flex items-center justify-between mb-3">
                                <div className="flex items-center gap-3">
                                  <div className="relative w-12 h-12 rounded-lg overflow-hidden bg-gray-100 flex-shrink-0">
                                    {member.image ? (
                                      <img
                                        src={member.image}
                                        alt={member.name}
                                        className="w-full h-full object-cover"
                                        onError={(e) => {
                                          ;(e.target as HTMLImageElement).style.display = "none"
                                          const parent = e.target.parentElement
                                          if (parent) {
                                            parent.innerHTML =
                                              '<div class="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500"><User size={20} /></div>'
                                          }
                                        }}
                                      />
                                    ) : (
                                      <div className="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500">
                                        <User size={20} />
                                      </div>
                                    )}
                                  </div>
                                  <div className="min-w-0 flex-1">
                                    <h4 className="font-semibold text-gray-900 truncate">{member.name}</h4>
                                    <p className="text-sm text-gray-600 truncate">{member.designation}</p>
                                    <p className="text-xs text-gray-500">Main Tab</p>
                                  </div>
                                </div>
                                <div className="flex items-center gap-2 flex-shrink-0">
                                  <button
                                    onClick={() => setEditingMember(member)}
                                    className="p-2 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg transition-colors"
                                    title="Edit"
                                  >
                                    <Edit size={16} />
                                  </button>
                                  <button
                                    onClick={() => handleDeleteMember(member.id, member.imagePath)}
                                    className="p-2 text-gray-600 hover:text-red-600 hover:bg-gray-100 rounded-lg transition-colors"
                                    title="Delete"
                                  >
                                    <Trash2 size={16} />
                                  </button>
                                </div>
                              </div>
                              <div className="flex items-center justify-between text-sm text-gray-600">
                                <div className="flex items-center gap-2">
                                  <span>Order: {member.order + 1}</span>
                                  <MapPin size={12} />
                                  <span className="truncate max-w-[100px]">{member.location}</span>
                                </div>
                                <div className="flex items-center gap-1">
                                  <button
                                    onClick={() => handleReorder(index, "up", mainTab.id)}
                                    className="p-1 hover:bg-gray-100 rounded disabled:opacity-30"
                                    disabled={index === 0}
                                    title="Move up"
                                  >
                                    <ArrowUp size={14} />
                                  </button>
                                  <button
                                    onClick={() => handleReorder(index, "down", mainTab.id)}
                                    className="p-1 hover:bg-gray-100 rounded disabled:opacity-30"
                                    disabled={index === getMembersForTab(mainTab.id).length - 1}
                                    title="Move down"
                                  >
                                    <ArrowDown size={14} />
                                  </button>
                                </div>
                              </div>
                            </div>
                          ))}

                          {getMembersForTab(mainTab.id).length === 0 && (
                            <div className="text-center py-6 text-gray-500 border border-gray-200 rounded-lg">
                              <User className="mx-auto mb-2 text-gray-400" />
                              No team members in this tab
                            </div>
                          )}
                        </div>
                      )}

                      {/* Sub Tabs */}
                      {getSubTabs(mainTab.id).map((subTab) => (
                        <div key={subTab.id} className="ml-8 mb-6">
                          <div className="flex items-center justify-between mb-4">
                            <div className="flex items-center gap-3">
                              <button
                                onClick={() => toggleExpandTab(subTab.id)}
                                className="p-1 hover:bg-gray-100 rounded-lg transition-colors"
                              >
                                {expandedTabs.includes(subTab.id) ? (
                                  <ChevronDown size={20} />
                                ) : (
                                  <ChevronRight size={20} />
                                )}
                              </button>
                              <h4 className="text-md font-medium text-gray-800 flex items-center gap-2">
                                <FolderOpen size={18} className="text-gray-500" />
                                {subTab.name}
                              </h4>
                              <span className="bg-gray-100 text-gray-800 text-sm font-medium px-3 py-1 rounded-full">
                                {getMembersForTab(mainTab.id, subTab.id).length} members
                              </span>
                            </div>
                          </div>

                          {/* Sub Tab Members (if expanded) */}
                          {expandedTabs.includes(subTab.id) && (
                            <div className="ml-8">
                              {getMembersForTab(mainTab.id, subTab.id).map((member, index) => (
                                <div
                                  key={member.id}
                                  className="border border-gray-200 rounded-lg p-4 hover:border-gray-300 transition-colors mb-3"
                                >
                                  <div className="flex items-center justify-between mb-3">
                                    <div className="flex items-center gap-3">
                                      <div className="relative w-12 h-12 rounded-lg overflow-hidden bg-gray-100 flex-shrink-0">
                                        {member.image ? (
                                          <img
                                            src={member.image}
                                            alt={member.name}
                                            className="w-full h-full object-cover"
                                            onError={(e) => {
                                              ;(e.target as HTMLImageElement).style.display = "none"
                                              const parent = e.target.parentElement
                                              if (parent) {
                                                parent.innerHTML =
                                                  '<div class="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500"><User size={20} /></div>'
                                              }
                                            }}
                                          />
                                        ) : (
                                          <div className="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500">
                                            <User size={20} />
                                          </div>
                                        )}
                                      </div>
                                      <div className="min-w-0 flex-1">
                                        <h4 className="font-semibold text-gray-900 truncate">{member.name}</h4>
                                        <p className="text-sm text-gray-600 truncate">{member.designation}</p>
                                        <p className="text-xs text-gray-500">Sub Tab: {subTab.name}</p>
                                      </div>
                                    </div>
                                    <div className="flex items-center gap-2 flex-shrink-0">
                                      <button
                                        onClick={() => setEditingMember(member)}
                                        className="p-2 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg transition-colors"
                                        title="Edit"
                                      >
                                        <Edit size={16} />
                                      </button>
                                      <button
                                        onClick={() => handleDeleteMember(member.id, member.imagePath)}
                                        className="p-2 text-gray-600 hover:text-red-600 hover:bg-gray-100 rounded-lg transition-colors"
                                        title="Delete"
                                      >
                                        <Trash2 size={16} />
                                      </button>
                                    </div>
                                  </div>
                                  <div className="flex items-center justify-between text-sm text-gray-600">
                                    <div className="flex items-center gap-2">
                                      <span>Order: {member.order + 1}</span>
                                      <MapPin size={12} />
                                      <span className="truncate max-w-[100px]">{member.location}</span>
                                    </div>
                                    <div className="flex items-center gap-1">
                                      <button
                                        onClick={() => handleReorder(index, "up", mainTab.id, subTab.id)}
                                        className="p-1 hover:bg-gray-100 rounded disabled:opacity-30"
                                        disabled={index === 0}
                                        title="Move up"
                                      >
                                        <ArrowUp size={14} />
                                      </button>
                                      <button
                                        onClick={() => handleReorder(index, "down", mainTab.id, subTab.id)}
                                        className="p-1 hover:bg-gray-100 rounded disabled:opacity-30"
                                        disabled={index === getMembersForTab(mainTab.id, subTab.id).length - 1}
                                        title="Move down"
                                      >
                                        <ArrowDown size={14} />
                                      </button>
                                    </div>
                                  </div>
                                </div>
                              ))}

                              {getMembersForTab(mainTab.id, subTab.id).length === 0 && (
                                <div className="text-center py-6 text-gray-500 border border-gray-200 rounded-lg">
                                  <User className="mx-auto mb-2 text-gray-400" />
                                  No team members in this sub tab
                                </div>
                              )}
                            </div>
                          )}
                        </div>
                      ))}
                    </div>
                  ))}

                  {getMainTabs().length === 0 && (
                    <div className="text-center py-12">
                      <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                        <Users className="text-gray-400" size={24} />
                      </div>
                      <p className="text-gray-500">No tabs created yet.</p>
                      <p className="text-sm text-gray-400 mt-1">
                        Go to Tab Structure to create your first tab, then add team members here.
                      </p>
                    </div>
                  )}
                </div>
              </div>
            )}

            {/* TAB STRUCTURE TAB */}
            {activeTab === "structure" && (
              <div className="space-y-6">
                {/* Add New Tab Form */}
                <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
                  <h2 className="text-xl font-bold text-gray-900 mb-4">Add New Tab</h2>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Tab Name *</label>
                      <input
                        type="text"
                        value={newTab.name}
                        onChange={(e) => setNewTab({ ...newTab, name: e.target.value })}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        placeholder="Enter tab name"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Tab Type *</label>
                      <select
                        value={newTab.type}
                        onChange={(e) => {
                          const type = e.target.value as "main" | "sub"
                          setNewTab({
                            ...newTab,
                            type,
                            parentId: type === "main" ? "" : newTab.parentId
                          })
                        }}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                      >
                        <option value="main">Main Tab</option>
                        <option value="sub">Sub Tab</option>
                      </select>
                    </div>

                    {newTab.type === "sub" && (
                      <div className="md:col-span-2">
                        <label className="block text-sm font-medium text-gray-700 mb-1">Parent Tab *</label>
                        <select
                          value={newTab.parentId}
                          onChange={(e) => setNewTab({ ...newTab, parentId: e.target.value })}
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        >
                          <option value="">Select parent tab</option>
                          {getMainTabs().map((tab) => (
                            <option key={tab.id} value={tab.id}>{tab.name}</option>
                          ))}
                        </select>
                      </div>
                    )}
                  </div>

                  <div className="flex items-center gap-4 mt-6">
                    <button
                      onClick={handleAddTab}
                      disabled={!newTab.name.trim() || (newTab.type === "sub" && !newTab.parentId)}
                      className={`px-6 py-3 rounded-lg font-semibold transition-colors flex items-center gap-2 ${
                        !newTab.name.trim() || (newTab.type === "sub" && !newTab.parentId)
                          ? "bg-gray-400 cursor-not-allowed"
                          : "bg-black text-white hover:bg-gray-800"
                      }`}
                    >
                      <Plus size={18} />
                      Add Tab
                    </button>
                    <button
                      onClick={clearTabForm}
                      className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors"
                    >
                      Clear Form
                    </button>
                  </div>
                </div>

                {/* Existing Tabs */}
                <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
                  <div className="flex items-center justify-between mb-6">
                    <h2 className="text-xl font-bold text-gray-900">Existing Tabs</h2>
                    <span className="bg-gray-100 text-gray-800 text-sm font-medium px-3 py-1 rounded-full">
                      {tabs.length} tabs
                    </span>
                  </div>

                  {tabs.length === 0 ? (
                    <div className="text-center py-8 text-gray-500">
                      <Layers className="mx-auto mb-2 text-gray-400" size={32} />
                      <p>No tabs yet. Create your first tab above.</p>
                    </div>
                  ) : (
                    <div className="space-y-4">
                      {getMainTabs().map((tab, index) => (
                        <div key={tab.id} className="border border-gray-200 rounded-lg p-4">
                          <div className="flex items-center justify-between">
                            <div className="flex items-center gap-3">
                              <div className="flex items-center gap-2">
                                <button
                                  onClick={() => handleReorderTab(tabs.findIndex(t => t.id === tab.id), "up")}
                                  disabled={index === 0}
                                  className="p-1 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg disabled:opacity-30"
                                  title="Move up"
                                >
                                  <ArrowUp size={16} />
                                </button>
                                <button
                                  onClick={() => handleReorderTab(tabs.findIndex(t => t.id === tab.id), "down")}
                                  disabled={index === getMainTabs().length - 1}
                                  className="p-1 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg disabled:opacity-30"
                                  title="Move down"
                                >
                                  <ArrowDown size={16} />
                                </button>
                              </div>
                              <div className="flex items-center gap-2">
                                <Folder className="text-gray-500" size={20} />
                                <div>
                                  <h3 className="font-semibold text-gray-900">{tab.name}</h3>
                                  <p className="text-sm text-gray-600">Main Tab • Order: {tab.order + 1}</p>
                                </div>
                              </div>
                            </div>
                            <div className="flex items-center gap-2">
                              <button
                                onClick={() => setEditingTab(tab)}
                                className="p-2 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg transition-colors"
                                title="Edit"
                              >
                                <Edit size={16} />
                              </button>
                              <button
                                onClick={() => handleDeleteTab(tab)}
                                className="p-2 text-gray-600 hover:text-red-600 hover:bg-gray-100 rounded-lg transition-colors"
                                title="Delete"
                              >
                                <Trash2 size={16} />
                              </button>
                            </div>
                          </div>

                          {/* Sub Tabs for this Main Tab */}
                          {getSubTabs(tab.id).length > 0 && (
                            <div className="mt-4 ml-8 space-y-3">
                              {getSubTabs(tab.id).map((subTab, subIndex) => (
                                <div key={subTab.id} className="border border-gray-200 rounded-lg p-3">
                                  <div className="flex items-center justify-between">
                                    <div className="flex items-center gap-3">
                                      <div className="flex items-center gap-2">
                                        <button
                                          onClick={() => handleReorderTab(tabs.findIndex(t => t.id === subTab.id), "up")}
                                          disabled={subIndex === 0}
                                          className="p-1 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg disabled:opacity-30"
                                          title="Move up"
                                        >
                                          <ArrowUp size={14} />
                                        </button>
                                        <button
                                          onClick={() => handleReorderTab(tabs.findIndex(t => t.id === subTab.id), "down")}
                                          disabled={subIndex === getSubTabs(tab.id).length - 1}
                                          className="p-1 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg disabled:opacity-30"
                                          title="Move down"
                                        >
                                          <ArrowDown size={14} />
                                        </button>
                                      </div>
                                      <div className="flex items-center gap-2">
                                        <FolderOpen className="text-gray-500" size={16} />
                                        <div>
                                          <h4 className="font-medium text-gray-800">{subTab.name}</h4>
                                          <p className="text-xs text-gray-600">Sub Tab • Order: {subTab.order + 1}</p>
                                        </div>
                                      </div>
                                    </div>
                                    <div className="flex items-center gap-2">
                                      <button
                                        onClick={() => setEditingTab(subTab)}
                                        className="p-1 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg transition-colors"
                                        title="Edit"
                                      >
                                        <Edit size={14} />
                                      </button>
                                      <button
                                        onClick={() => handleDeleteTab(subTab)}
                                        className="p-1 text-gray-600 hover:text-red-600 hover:bg-gray-100 rounded-lg transition-colors"
                                        title="Delete"
                                      >
                                        <Trash2 size={14} />
                                      </button>
                                    </div>
                                  </div>
                                </div>
                              ))}
                            </div>
                          )}
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>
            )}

            {/* BANNER MANAGEMENT TAB */}
            {activeTab === "banners" && (
              <div className="space-y-6">
                {/* Add New Banner Form */}
                <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
                  <h2 className="text-xl font-bold text-gray-900 mb-4">Add New Banner</h2>
                  
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">
                        Title (Optional)
                      </label>
                      <input
                        type="text"
                        value={newBanner.title}
                        onChange={(e) => setNewBanner({...newBanner, title: e.target.value})}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        placeholder="Banner title"
                        disabled={uploading}
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">
                        Description (Optional)
                      </label>
                      <input
                        type="text"
                        value={newBanner.description}
                        onChange={(e) => setNewBanner({...newBanner, description: e.target.value})}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        placeholder="Brief description"
                        disabled={uploading}
                      />
                    </div>

                    <div className="md:col-span-2">
                      <label className="block text-sm font-medium text-gray-700 mb-1">
                        Banner Image *
                      </label>
                      <div className="flex flex-col gap-4">
                        <div className="flex items-center gap-4">
                          <label className={`flex items-center gap-2 px-4 py-2 rounded-lg cursor-pointer transition-colors ${
                            uploading ? "bg-gray-400 cursor-not-allowed" : "bg-black text-white hover:bg-gray-800"
                          }`}>
                            <Upload size={18} />
                            {uploading ? "Uploading..." : "Select Image"}
                            <input
                              type="file"
                              accept="image/*"
                              onChange={(e) => handleFileChange(e, false, true)}
                              className="hidden"
                              disabled={uploading}
                            />
                          </label>
                          {bannerImageFile && (
                            <div className="flex items-center gap-2">
                              <span className="text-sm text-gray-600">
                                Selected: {bannerImageFile.name}
                              </span>
                              <button
                                onClick={() => {
                                  setBannerImageFile(null)
                                  if (bannerPreviewUrl) {
                                    URL.revokeObjectURL(bannerPreviewUrl)
                                    setBannerPreviewUrl(null)
                                  }
                                }}
                                className="p-1 text-red-500 hover:text-red-700"
                                disabled={uploading}
                              >
                                <X size={16} />
                              </button>
                            </div>
                          )}
                        </div>

                        {/* Banner Image Preview */}
                        {(bannerPreviewUrl) && (
                          <div className="mt-2">
                            <p className="text-sm font-medium text-gray-700 mb-2">Preview:</p>
                            <div className="flex flex-col md:flex-row items-start gap-4">
                              <div className="relative w-full md:w-96 h-48 rounded-lg overflow-hidden bg-gray-100">
                                <img
                                  src={bannerPreviewUrl}
                                  alt="Banner Preview"
                                  className="w-full h-full object-cover"
                                />
                              </div>
                              <div className="text-sm text-gray-600">
                                <p className="font-medium">Image Details:</p>
                                <p>• Will be uploaded to Firebase Storage</p>
                                <p>• Stored in "home-banners" folder</p>
                                <p>• Recommended: High-quality image, 1920x1080px, max 5MB</p>
                              </div>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>

                  <div className="flex items-center gap-4 mt-6">
                    <button
                      onClick={handleAddBanner}
                      disabled={uploading || !bannerImageFile}
                      className={`px-6 py-3 rounded-lg font-semibold transition-colors flex items-center gap-2 ${
                        uploading || !bannerImageFile 
                          ? "bg-gray-400 cursor-not-allowed" 
                          : "bg-black text-white hover:bg-gray-800"
                      }`}
                    >
                      {uploading ? (
                        <>
                          <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                          Processing...
                        </>
                      ) : (
                        <>
                          <Plus size={18} />
                          Add Banner
                        </>
                      )}
                    </button>
                    <button
                      onClick={clearBannerForm}
                      disabled={uploading}
                      className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors disabled:opacity-50"
                    >
                      Clear Form
                    </button>
                  </div>
                </div>

                {/* Existing Banners */}
                <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
                  <div className="flex items-center justify-between mb-4">
                    <h2 className="text-xl font-bold text-gray-900">Existing Banners</h2>
                    <span className="bg-gray-100 text-gray-800 text-sm font-medium px-3 py-1 rounded-full">
                      {banners.length} banners
                    </span>
                  </div>
                  
                  {banners.length === 0 ? (
                    <div className="text-center py-8 text-gray-500">
                      <Home className="mx-auto mb-2 text-gray-400" size={32} />
                      <p>No banners yet. Add your first banner above.</p>
                    </div>
                  ) : (
                    <div className="space-y-4">
                      {banners.map((banner, index) => (
                        <div key={banner.id} className="border border-gray-200 rounded-lg p-4">
                          <div className="flex flex-col md:flex-row gap-4">
                            {/* Banner Image */}
                            <div className="relative w-full md:w-64 h-48 rounded-lg overflow-hidden bg-gray-100">
                              <img
                                src={banner.imageUrl}
                                alt={banner.title || "Banner"}
                                className="w-full h-full object-cover"
                              />
                            </div>

                            {/* Banner Details */}
                            <div className="flex-1">
                              <div className="flex items-start justify-between mb-2">
                                <div>
                                  <h4 className="font-semibold text-gray-900">
                                    {banner.title || "Untitled Banner"}
                                  </h4>
                                  {banner.description && (
                                    <p className="text-sm text-gray-600 mt-1">{banner.description}</p>
                                  )}
                                </div>
                                <div className="flex items-center gap-2">
                                  <button
                                    onClick={() => handleToggleActive(banner)}
                                    className={`px-3 py-1 rounded-full text-xs font-medium flex items-center gap-1 ${
                                      banner.active 
                                        ? "bg-green-100 text-green-800" 
                                        : "bg-gray-100 text-gray-800"
                                    }`}
                                  >
                                    {banner.active ? (
                                      <>
                                        <Eye size={12} />
                                        Active
                                      </>
                                    ) : (
                                      <>
                                        <EyeOff size={12} />
                                        Inactive
                                      </>
                                    )}
                                  </button>
                                </div>
                              </div>

                              <div className="text-sm text-gray-600 space-y-1">
                                <p>Order: {banner.order + 1}</p>
                                <p>Status: {banner.active ? "Visible on home page" : "Hidden"}</p>
                                {banner.imagePath && (
                                  <p className="text-xs text-gray-500 truncate max-w-xs">
                                    Storage Path: {banner.imagePath}
                                  </p>
                                )}
                              </div>

                              {/* Actions */}
                              <div className="flex items-center gap-2 mt-4">
                                <button
                                  onClick={() => handleBannerReorder(index, "up")}
                                  disabled={index === 0}
                                  className="p-2 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg disabled:opacity-30"
                                  title="Move up"
                                >
                                  <ChevronUp size={16} />
                                </button>
                                <button
                                  onClick={() => handleBannerReorder(index, "down")}
                                  disabled={index === banners.length - 1}
                                  className="p-2 text-gray-600 hover:text-black hover:bg-gray-100 rounded-lg disabled:opacity-30"
                                  title="Move down"
                                >
                                  <DownIcon size={16} />
                                </button>
                                <button
                                  onClick={() => handleDeleteBanner(banner)}
                                  className="p-2 text-gray-600 hover:text-red-600 hover:bg-gray-100 rounded-lg ml-auto"
                                  title="Delete"
                                >
                                  <Trash2 size={16} />
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>
            )}

            {/* Edit Modal (Team Member) */}
            {editingMember && (
              <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
                <div className="bg-white rounded-xl shadow-lg max-w-2xl w-full max-h-[90vh] overflow-y-auto">
                  <div className="p-6 border-b border-gray-200">
                    <div className="flex items-center justify-between">
                      <h2 className="text-2xl font-bold text-gray-900">Edit Team Member</h2>
                      <button onClick={clearEditingForm} className="p-2 hover:bg-gray-100 rounded-lg" disabled={uploading}>
                        <X size={24} />
                      </button>
                    </div>
                  </div>
                  <div className="p-6">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Tab *</label>
                        <select
                          value={editingMember.tabId}
                          onChange={(e) => setEditingMember({ ...editingMember, tabId: e.target.value, subTabId: "" })}
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                          disabled={uploading}
                        >
                          <option value="">Select a tab</option>
                          {getMainTabs().map((tab) => (
                            <option key={tab.id} value={tab.id}>{tab.name}</option>
                          ))}
                        </select>
                      </div>

                      {editingMember.tabId && getSubTabs(editingMember.tabId).length > 0 && (
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Sub Tab (Optional)</label>
                          <select
                            value={editingMember.subTabId || ""}
                            onChange={(e) => setEditingMember({ ...editingMember, subTabId: e.target.value })}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                            disabled={uploading}
                          >
                            <option value="">None (Main Tab)</option>
                            {getSubTabs(editingMember.tabId).map((subTab) => (
                              <option key={subTab.id} value={subTab.id}>{subTab.name}</option>
                            ))}
                          </select>
                        </div>
                      )}

                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Name *</label>
                        <input
                          type="text"
                          value={editingMember.name}
                          onChange={(e) => setEditingMember({ ...editingMember, name: e.target.value })}
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                          disabled={uploading}
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Designation *</label>
                        <input
                          type="text"
                          value={editingMember.designation}
                          onChange={(e) => setEditingMember({ ...editingMember, designation: e.target.value })}
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                          disabled={uploading}
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Location *</label>
                        <input
                          type="text"
                          value={editingMember.location}
                          onChange={(e) => setEditingMember({ ...editingMember, location: e.target.value })}
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                          disabled={uploading}
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Display Order</label>
                        <input
                          type="number"
                          value={editingMember.order || 0}
                          onChange={(e) =>
                            setEditingMember({ ...editingMember, order: Number.parseInt(e.target.value) || 0 })
                          }
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                          min="0"
                          disabled={uploading}
                        />
                      </div>
                      <div className="md:col-span-2">
                        <label className="block text-sm font-medium text-gray-700 mb-1">Profile Image</label>
                        <div className="space-y-4">
                          <div className="flex items-center gap-4">
                            <label
                              className={`flex items-center gap-2 px-4 py-2 rounded-lg cursor-pointer transition-colors ${uploading ? "bg-gray-400 cursor-not-allowed" : "bg-black text-white hover:bg-gray-800"}`}
                            >
                              <Upload size={18} />
                              {uploading ? "Uploading..." : "Upload New Image"}
                              <input
                                type="file"
                                accept="image/*"
                                onChange={(e) => handleFileChange(e, true)}
                                className="hidden"
                                disabled={uploading}
                              />
                            </label>
                            {editingImageFile && (
                              <div className="flex items-center gap-2">
                                <span className="text-sm text-gray-600">
                                  New: {editingImageFile.name} ({(editingImageFile.size / 1024).toFixed(2)} KB)
                                </span>
                                <button
                                  onClick={() => {
                                    setEditingImageFile(null)
                                    if (editingPreviewUrl) {
                                      URL.revokeObjectURL(editingPreviewUrl)
                                      setEditingPreviewUrl(null)
                                    }
                                  }}
                                  className="p-1 text-red-500 hover:text-red-700"
                                  disabled={uploading}
                                >
                                  <X size={16} />
                                </button>
                              </div>
                            )}
                          </div>

                          {/* Current Image Preview */}
                          {editingMember.image && !editingPreviewUrl && (
                            <div>
                              <p className="text-sm font-medium text-gray-700 mb-2">Current Image:</p>
                              <div className="flex flex-wrap items-start gap-4">
                                <div className="relative w-32 h-32 border border-gray-300 rounded-lg overflow-hidden bg-gray-100">
                                  <img
                                    src={editingMember.image}
                                    alt="Current"
                                    className="w-full h-full object-cover"
                                    onError={(e) => {
                                      ;(e.target as HTMLImageElement).style.display = "none"
                                      const parent = e.target.parentElement
                                      if (parent) {
                                        parent.innerHTML =
                                          '<div class="w-full h-full flex items-center justify-center bg-gray-200 text-gray-500">No Image</div>'
                                      }
                                    }}
                                  />
                                </div>
                                <div className="text-sm text-gray-600">
                                  <p className="font-medium">Current Image Info:</p>
                                  <p>
                                    • Source: {editingMember.image.includes("firebasestorage.googleapis.com") ? "Firebase Storage" : "External"}
                                  </p>
                                  {editingMember.imagePath && (
                                    <p className="truncate max-w-xs text-xs">Storage Path: {editingMember.imagePath}</p>
                                  )}
                                </div>
                              </div>
                            </div>
                          )}

                          {/* New Image Preview */}
                          {editingPreviewUrl && (
                            <div>
                              <p className="text-sm font-medium text-gray-700 mb-2">New Image Preview:</p>
                              <div className="flex flex-wrap items-start gap-4">
                                <div className="relative w-32 h-32 border border-gray-300 rounded-lg overflow-hidden bg-gray-100">
                                  <img
                                    src={editingPreviewUrl}
                                    alt="New Preview"
                                    className="w-full h-full object-cover"
                                  />
                                </div>
                                <div className="text-sm text-gray-600">
                                  <p className="font-medium">Will replace current image</p>
                                  <p>• Upload to Firebase Storage on save</p>
                                  <p>• Old image will be automatically deleted</p>
                                </div>
                              </div>
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                    <div className="flex justify-end gap-3 mt-8">
                      <button
                        onClick={clearEditingForm}
                        disabled={uploading}
                        className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors disabled:opacity-50"
                      >
                        Cancel
                      </button>
                      <button
                        onClick={handleUpdateMember}
                        disabled={uploading}
                        className={`px-6 py-3 rounded-lg font-semibold transition-colors flex items-center gap-2 ${
                          uploading ? "bg-gray-400 cursor-not-allowed" : "bg-black text-white hover:bg-gray-800"
                        }`}
                      >
                        {uploading ? (
                          <>
                            <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                            Saving...
                          </>
                        ) : (
                          <>
                            <Save size={18} />
                            Save Changes
                          </>
                        )}
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            )}

            {/* Edit Modal (Tab) */}
            {editingTab && (
              <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
                <div className="bg-white rounded-xl shadow-lg max-w-md w-full">
                  <div className="p-6 border-b border-gray-200">
                    <div className="flex items-center justify-between">
                      <h2 className="text-2xl font-bold text-gray-900">Edit Tab</h2>
                      <button onClick={() => setEditingTab(null)} className="p-2 hover:bg-gray-100 rounded-lg">
                        <X size={24} />
                      </button>
                    </div>
                  </div>
                  <div className="p-6">
                    <div className="space-y-4">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Tab Name *</label>
                        <input
                          type="text"
                          value={editingTab.name}
                          onChange={(e) => setEditingTab({ ...editingTab, name: e.target.value })}
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                        />
                      </div>

                      {editingTab.type === "sub" && (
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">Parent Tab *</label>
                          <select
                            value={editingTab.parentId}
                            onChange={(e) => setEditingTab({ ...editingTab, parentId: e.target.value })}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-transparent"
                          >
                            <option value="">Select parent tab</option>
                            {getMainTabs().map((tab) => (
                              <option key={tab.id} value={tab.id}>{tab.name}</option>
                            ))}
                          </select>
                        </div>
                      )}
                    </div>
                    <div className="flex justify-end gap-3 mt-8">
                      <button
                        onClick={() => setEditingTab(null)}
                        className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors"
                      >
                        Cancel
                      </button>
                      <button
                        onClick={handleUpdateTab}
                        className="px-6 py-3 bg-black text-white rounded-lg font-semibold hover:bg-gray-800 transition-colors flex items-center gap-2"
                      >
                        <Save size={18} />
                        Save Changes
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}